import { DriveItem, Folder as DriveItemFolder } from "@microsoft/microsoft-graph-types";
import msGraph from "@/services/msGraph"
import graphConfig from "@/graphConfig"

type Node = {
    id: string;
    name: string | null | undefined;
    webUrl: string | null | undefined;
}
export type File = Node;
export type Folder = Node & {
    folders: Folder[];
    files: File[];
    /** Filtered files count. */
    count: number;
    /** Filtered files size. */
    size: number;
    /** Unfiltered files count. */
    unfilteredCount: number;
    /** Unfiltered files size. */
    unfilteredSize: number;
}

const appService = (() => {

    function newFolder(id: string, name: string | null = null, webUrl: string | null = null) {
        return {
            id,
            name,
            webUrl,
            folders: [] as Folder[],
            files: [] as File[],
            count: 0,
            size: 0,
            unfilteredCount: 0,
            unfilteredSize: 0
        } as Folder;
    }

    function getFolder(id: string) {
        return msGraph.get(`${graphConfig.graphEndpoint}/me/drive/items/${id}`, ["Files.Read"])
            .then((item) => {
                const driveItem = item as DriveItem;
                return newFolder(id, driveItem.name, driveItem.webUrl);
            });
    }

    function findRecursively(folder: Folder, predicate: (item: DriveItem) => boolean) {
        return msGraph.get(`${graphConfig.graphEndpoint}/me/drive/items/${folder.id}/children`, ["Files.Read"])
            .then(async (response) => {
                const driveItems = response.value as DriveItem[];
                let childrenPromises = Array<Promise<any>>();
                let childrenLength = 0;
                let child: Folder;
                for (let i = 0; i < driveItems.length; i++) {
                    if (driveItems[i].folder) { // Folder
                        child = newFolder(driveItems[i].id as string,
                            driveItems[i].name,
                            driveItems[i].webUrl);
                        childrenLength = childrenPromises.push(findRecursively(child, predicate));
                    }
                    else { // File
                        if (predicate(driveItems[i])) {
                            folder.files.push({
                                id: driveItems[i].id as string,
                                name: driveItems[i].name,
                                webUrl: driveItems[i].webUrl
                            });
                            folder.count++;
                            folder.size += driveItems[i].size as number;
                        }
                        folder.unfilteredCount++;
                        folder.unfilteredSize += driveItems[i].size as number;
                    }
                }
                // awaiting multiple values (https://medium.com/@bluepnume/learn-about-promises-before-you-start-using-async-await-eb148164a9c8)
                let children = await Promise.all(childrenPromises);
                for (let i = 0; i < children.length; i++) {
                    if (children[i].files.length > 0 || children[i].folders.length > 0) {
                        folder.folders.push(children[i]);
                        folder.count += children[i].count;
                        folder.size += children[i].size;
                    }
                    folder.unfilteredCount += children[i].unfilteredCount;
                    folder.unfilteredSize += children[i].unfilteredSize;
                }
                return folder;
            });
    }

    async function findMissingHashValues(folderId: string) {
        return getFolder(folderId)
            .then(folder => {
                const hasHash = (item: DriveItem) => !item.file?.hashes?.quickXorHash;
                return findRecursively(folder, hasHash)
                    .then(response => folderWithChildrenOrNull(response));
            });
    }
    function folderWithChildrenOrNull(folder: Folder) {
        return folder.files.length == 0 && folder.folders.length == 0 ? null : folder;
    }
    async function getHashes(folderId: string) {
        return msGraph.get(`${graphConfig.graphEndpoint}/me/drive/items/${folderId}/children`, ["Files.Read"])
            .then(async (response) => {
                const driveItems = response.value as DriveItem[];
                const files = driveItems.filter(item => item.file,);
                const folders = driveItems.filter(item => item.folder);
                let hashes = new Set<string>();
                files.forEach((item) => {
                    if (item.file?.hashes?.quickXorHash)
                        hashes.add(item.file?.hashes?.quickXorHash as string);
                })
                const promises: Promise<Set<string>>[] = [];
                folders.forEach(item => {
                    promises.push(getHashes(item.id as string));
                });
                let values = await Promise.all(promises);
                values.forEach(value => {
                    if (hashes.size == 0)
                        hashes = value;
                    else
                        hashes = new Set([...hashes, ...value]);
                });
                return hashes;
            });
    }
    async function getHashesMap(folderId: string) {
        return msGraph.get(`${graphConfig.graphEndpoint}/me/drive/items/${folderId}/children`, ["Files.Read"])
            .then(async (response) => {
                const driveItems = response.value as DriveItem[];
                const files = driveItems.filter(item => item.file,);
                const folders = driveItems.filter(item => item.folder);
                let hashes = new Map<string, boolean>();
                files.forEach(item => {
                    const hash = item.file?.hashes?.quickXorHash;
                    if (hash)
                        hashes.set(hash, hashes.has(hash));
                });
                const promises: Promise<Map<string, boolean>>[] = [];
                folders.forEach(item => {
                    promises.push(getHashesMap(item.id as string));
                });
                let values = await Promise.all(promises);
                values.forEach(map => {
                    map.forEach((value: boolean, key: string) => {
                        hashes.set(key, value || hashes.has(key));
                    });
                });
                return hashes;
            });
    }

    async function findDuplicateFiles(folderId: string) {
        return getHashesMap(folderId)
            .then((map: Map<string, boolean>) => {
                const hashes = new Set<string>();
                map.forEach((value, key) => {
                    if (value)
                        hashes.add(key);
                });
                return getFolder(folderId)
                    .then(folder => {
                        const predicate = (item: DriveItem) => {
                            let hash = item.file?.hashes?.quickXorHash;
                            return Boolean(hash && hashes.has(hash));
                        };
                        return findRecursively(folder, predicate)
                            .then(response => folderWithChildrenOrNull(response));
                    });
            });
    }

    /**
     * Find files in secondary folder wich already exist in primary folder.
     * @param primaryId 
     * @param secondaryId 
     */
    async function compareFolders(primaryId: string, secondaryId: string) {
        let folder = newFolder(secondaryId);
        return getHashes(primaryId)
            .then(hashes => {
                let i: number = 0;
                const isDuplicate = (item: DriveItem) => {
                    let hash: string | null = item.file?.hashes?.quickXorHash || null;
                    return Boolean(hash && hashes.has(hash));
                }
                return findRecursively(folder, isDuplicate)
                    .then(response => {
                        folder = response;
                        if (folder.files.length == 0 && folder.folders.length == 0)
                            return null;
                        else
                            return msGraph.get(`${graphConfig.graphEndpoint}/me/drive/items/${secondaryId}`, ["Files.Read"])
                                .then(item => {
                                    const driveItem = item as DriveItem;
                                    folder.name = driveItem.name;
                                    folder.webUrl = driveItem.webUrl;
                                    return folder;
                                })
                    })
            });
    }

    function deleteFolder(folder: Folder, isTop: boolean) {
        if (folder.count == folder.unfilteredCount && !isTop) {
            msGraph.delete(
                `${graphConfig.graphEndpoint}/me/drive/items/${folder.id}`,
                ["Files.ReadWrite"]
            );
        }
        else {
            folder.files.forEach(file =>
                msGraph.delete(
                    `${graphConfig.graphEndpoint}/me/drive/items/${file.id}`,
                    ["Files.ReadWrite"]
                ));
            folder.folders.forEach(child => deleteFolder(child, false));
        }
    }

    return {
        /** Drive root folders
         * @returns DriveItem[]
         */
        getRootFolders: () => msGraph.get(
            `${graphConfig.graphEndpoint}/me/drive/root/children`,
            ["Files.Read"])
            .then(response => response.value.filter((item: DriveItem) => item.folder) as DriveItem[]),
        findMissingHashValues,
        findDuplicateFiles,
        compareFolders,
        delete: deleteFolder
    };
})();

export default appService;