<template>
  <SignIn @onAuthRedirect="onAuthRedirect" />
  <div v-if="isAuthenticated">
    <p v-if="showRootFolders">
      Root folders:<br />
      <template v-for="folder in rootFolders" :key="folder.id">
        <a :href="folder.webUrl" target="_blank" :title="folder.id">{{
          folder.name
        }}</a
        >, {{ formatBytes(folder.size) }} <br />
      </template>
    </p>

    <p>
      <label for="primaryFolder">Primary folder:</label><br />
      <select
        id="primaryFolder"
        v-model="primaryFolder"
        :key="primaryFolder"
        v-bind:disabled="isLoading"
      >
        <option selected></option>
        <option
          v-for="folder in rootFolders"
          :value="folder.id"
          :key="folder.id"
        >
          {{ folder.name }}
        </option>
      </select>
      <span v-html="' '" />
      <button
        type="button"
        @click="findMissingHashValues()"
        v-show="Boolean(primaryFolder)"
        v-bind:disabled="isLoading"
      >
        Find missing hash values
      </button>
      <span v-html="' '" />
      <button
        type="button"
        @click="findDuplicateFiles()"
        v-show="Boolean(primaryFolder)"
        v-bind:disabled="isLoading"
      >
        Find duplicate files</button
      ><br />

      <label for="secondaryFolder">Secondary folder:</label><br />
      <select
        id="secondaryFolder"
        v-model="secondaryFolder"
        v-bind:disabled="isLoading"
      >
        <option selected></option>
        <option
          v-for="folder in rootFolders"
          :value="folder.id"
          :key="folder.id"
        >
          {{ folder.name }}
        </option>
      </select>
      <span v-html="' '" />
      <button
        type="button"
        @click="compareFolders"
        v-show="
          Boolean(primaryFolder) &&
          Boolean(secondaryFolder) &&
          primaryFolder != secondaryFolder
        "
        title="Compare folders and find duplicate files"
        v-bind:disabled="isLoading"
      >
        Compare folders</button
      ><br />
    </p>

    <p>
      <span :innerText="message" />
      <loader size="32px" v-show="isLoading" />
    </p>

    <p v-if="showFolders">
      <Folders
        :data="folderData"
        :isTop="true"
        :allowDeletion="allowDeletion"
      />
    </p>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import SignIn from "@/components/SignIn.vue";
import Folders from "@/components/Folders.vue";
import Loader from "@/components/Loader.vue";
import signService from "@/services/signService";
import appService, { File, Folder } from "@/services/appService";
import formatBytes from "@/services/formatBytes";
import {
  DriveItem,
  Folder as DriveItemFolder,
} from "@microsoft/microsoft-graph-types";

export default defineComponent({
  name: "App",
  components: {
    SignIn,
    Folders,
    Loader,
  },
  setup() {
    const isAuthenticated = ref(false);
    const rootFolders = ref([] as DriveItem[]);
    const primaryFolder = ref("");
    const secondaryFolder = ref("");
    const folderData = ref<Folder | null>(null);
    const isLoading = ref(false);
    const message = ref("");
    const allowDeletion = ref(false);

    function beginLoad() {
      isLoading.value = true;
      clearMessage();
    }
    function endLoad(msg: string = "") {
      isLoading.value = false;
      message.value = msg;
    }
    function clearMessage() {
      message.value = "";
    }

    function onAuthRedirect(value: boolean) {
      isAuthenticated.value = value;
      if (value) {
        beginLoad();
        appService
          .getRootFolders()
          .then((response) => {
            rootFolders.value = response;
          })
          .finally(() => {
            endLoad();
          });
      }
    }

    function fillFolderData(
      promise: Promise<Folder | null>,
      dataMessage: string,
      noDataMessage: string
    ) {
      isLoading.value = true;
      clearMessage();
      promise
        .then((folder) => {
          folderData.value = folder;
          message.value = folder ? dataMessage : noDataMessage;
        })
        .catch((reason) => {
          message.value = reason;
        })
        .finally(() => {
          isLoading.value = false;
        });
    }

    return {
      isAuthenticated,
      onAuthRedirect,
      rootFolders,
      primaryFolder,
      secondaryFolder,
      folderData,
      isLoading,
      message,
      allowDeletion,
      formatBytes: (bytes: number) => {
        return formatBytes(bytes, 2);
      },
      clearMessage,
      findMissingHashValues: () => {
        allowDeletion.value = false;
        fillFolderData(
          appService.findMissingHashValues(primaryFolder.value),
          "Missing hash values found.",
          "There are no missing hash values."
        );
      },
      findDuplicateFiles: () => {
        allowDeletion.value = false;
        fillFolderData(
          appService.findDuplicateFiles(primaryFolder.value),
          "Duplicate files found.",
          "There are no duplicate files."
        );
      },
      compareFolders: () => {
        allowDeletion.value = true;
        fillFolderData(
          appService.compareFolders(primaryFolder.value, secondaryFolder.value),
          "Secondary folder files found in primary folder.",
          "There are no secondary folder files in primary folder."
        );
      },
      get showFolders() {
        return !isLoading.value && folderData.value;
      },
      get showRootFolders() {
        return isAuthenticated.value && rootFolders.value.length > 0;
      },
    };
  },
});
</script>

