import { PublicClientApplication, AccountInfo, SilentRequest, InteractionRequiredAuthError, AuthenticationResult } from "@azure/msal-browser";
import msalConfig from "@/msalConfig";
import signService from '@/services/signService';

const msGraph = (() => {

    const msalInstance = new PublicClientApplication(msalConfig);

    function getTokenRedirect(request: SilentRequest) {
        /**
         * See here for more info on account retrieval: 
         * https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-common/docs/Accounts.md
         */
        //request.account = msalInstance.getAccountByUsername(username as string) as AccountInfo;
        request.account = msalInstance.getAccountByUsername(signService.username as string) as AccountInfo;

        return msalInstance.acquireTokenSilent(request)
            .catch(error => {
                console.warn("silent token acquisition fails. acquiring token using redirect");
                if (error instanceof InteractionRequiredAuthError) {
                    // fallback to interaction when silent call fails
                    return msalInstance.acquireTokenRedirect(request);
                } else {
                    console.warn(error);
                }
            });
    }

    /** 
     * Helper function to call MS Graph API endpoint
     * using the authorization bearer token scheme
     * */
    function msGraphGet(endpoint: RequestInfo, scopes: string[]) {
        const request = { scopes } as SilentRequest;

        return getTokenRedirect(request)
            .then(response => {
                const accessToken = (response as AuthenticationResult).accessToken;
                const headers = new Headers();
                headers.append("Authorization", `Bearer ${accessToken}`);
                const options = {
                    method: "GET",
                    headers
                };
                return fetch(endpoint, options)
                    .then(response => response.json());
            });
    }

    function msGraphDelete(endpoint: RequestInfo, scopes: string[]) {
        const request = { scopes } as SilentRequest;

        return getTokenRedirect(request)
            .then(response => {
                const accessToken = (response as AuthenticationResult).accessToken;
                const headers = new Headers();
                headers.append("Authorization", `Bearer ${accessToken}`);
                const options = {
                    method: "DELETE",
                    headers
                };
                return fetch(endpoint, options);
            });
    }

    return {
        get: msGraphGet,
        delete: msGraphDelete
    };
})();

export default msGraph;