
import { defineComponent, ref, reactive } from "vue";
import signService from "@/services/signService";

export default defineComponent({
  name: "SignIn",
  setup(props, { emit }) {
    // data
    const userName = ref("");
    const isAuthenticated = ref(false);

    // methods
    const signIn = () => {
      // I like redirect rather than popup but I have no reason to.
      signService.loginRedirect();
    };
    const signOut = () => {
      signService.logout().then((response) => {
        isAuthenticated.value = false;
        userName.value = "";
        emit("onAuthRedirect", isAuthenticated.value);
      });
    };

    //// created()

    // Handle the redirect flows
    signService
      .handleRedirect()
      .then((tokenResponse: any) => {
        // Handle redirect response
        userName.value = tokenResponse ? tokenResponse.account?.username : null;
        isAuthenticated.value = Boolean(tokenResponse && userName.value);
        emit("onAuthRedirect", isAuthenticated.value);
      })
      .catch((error: any) => {
        userName.value = "Authentication error!";
        isAuthenticated.value = false;
        console.warn(error);
      });

    return {
      // props
      isAuthenticated,
      userName,
      // methods
      signIn,
      signOut,
    };
  },
});
