<template>
  <details :open="isTop">
    <summary v-if="isDeleted">'{{ data.name }}' deleted!</summary>
    <summary v-else>
      <a :href="data.webUrl" target="_blank">{{ data.name }}</a> ({{
        data.count
      }}
      of {{ data.unfilteredCount }} files, {{ formatBytes(data.size) }} of
      {{ formatBytes(data.unfilteredSize) }}, {{ data.size }} of
      {{ data.unfilteredSize }} B)
      <button
        title="Delete"
        @click="confirmDeletion"
        v-if="allowDeletion && !isConfirmingDeletion"
      >
        âŒ«
      </button>
      <button
        type="button"
        @click="cancelDeletion"
        v-show="isConfirmingDeletion"
      >
        Cancel
      </button>
      <span v-show="isConfirmingDeletion" v-html="' '" />
      <button type="button" @click="deleteFolder" v-show="isConfirmingDeletion">
        Delete
      </button>
    </summary>
    <div v-show="!isDeleted">
      <template v-for="file in data.files" :key="file.id">
        <a
          style="margin-left: 17px; display: inline-block"
          :href="file.webUrl"
          target="_blank"
          >{{ file.name }}</a
        ><br />
      </template>
      <folders
        style="margin-left: 17px"
        v-for="folder in data.folders"
        :key="folder.id"
        :data="folder"
        :isTop="false"
        v-bind:allowDeletion="allowDeletion"
      />
    </div>
  </details>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from "vue";
import formatBytes from "@/services/formatBytes";
import appService, { Folder, File } from "@/services/appService";

export default defineComponent({
  name: "Folders",
  props: {
    data: Object,
    isTop: Boolean,
    allowDeletion: Boolean,
  },
  setup(props) {
    const isConfirmingDeletion = ref(false);
    const isDeleted = ref(false);
    return {
      isConfirmingDeletion,
      isDeleted,
      formatBytes,
      deleteFolder: () => {
        appService.delete(props.data as Folder, props.isTop);
        isDeleted.value = true;
      },
      confirmDeletion: () => {
        isConfirmingDeletion.value = true;
      },
      cancelDeletion: () => {
        isConfirmingDeletion.value = false;
      },
    };
  },
});
</script>

<style scoped>
details details {
  margin-top: 1px;
}
</style>